import React from "react"
import PropTypes from 'prop-types'
import { Col, Modal, ModalBody, Row } from "reactstrap"
import { format } from 'date-fns';

const DetailModal = ({ show, onCloseClick ,event}) => {
  return (
    <Modal isOpen={show} toggle={onCloseClick} centered={true}>
      <ModalBody className="py-3 px-5">
        <Row className="mb-3">
          <Col lg={12}>
            <div >
              <i className="bx bx-calendar-event text-info display-5" style={{ lineHeight : '1'}}/>
          
            </div>
          </Col>
        </Row>
        <Row className="mb-3">
          <Col lg={12}>
            <div>
             <div><strong> Libelle:      </strong> <span>{event.libelle}</span></div>
             <div><strong> Lieu:         </strong><span>{event.lieu}</span></div>
             <div><strong> Dbut :       </strong> <span>{event.heure_debut}</span></div>
             <div><strong> Fin :         </strong><span>{event.heure_fin}</span></div>
             <div><strong> Description : </strong><span>{event.description}</span></div>
            </div>
          </Col>
        </Row>
        <Row>
          <Col>
            <div className="mt-3" style={{float:"right"}}>
              
              <button
                type="button"
                className="btn btn-info btn-sm me-2"
                onClick={onCloseClick}
              >
                Fermer
              </button>
            </div>
          </Col>
        </Row>
      </ModalBody>
    </Modal>
  )
}

DetailModal.propTypes = {
  onCloseClick: PropTypes.func,
  event: PropTypes.any,
  show: PropTypes.any
}

export default DetailModal
